@import url('./Common.css');

#listp{
    background-color: #fff;
    width: 100%; height: 100%;
    margin-top: 80px;
}
#listtit{
    width: 100%; height: 400px;
    position: relative;
}
#listtit::before{
    content: '';
    width:78%;
    border-bottom:2px solid #F93700;
    position: absolute;
    right: 0; top: 39%;
}
#listtit h1{
    color:#F93700;
    font-size: 5em;
    text-align: end;

    position: absolute;
    left: 7%; bottom:4%;
    transform: rotate(270deg) translateY(100%);
    transform-origin: 0 100%;
}

#listcon{
    width: 100%;
}
#listcon ul{
    width: 75%;
    margin-left: 20%;
    padding:0;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    overflow: hidden;
}
#listcon ul li{
    width:46%; height: 100%;
    margin-bottom:10%;
}
#listcon ul li figure{
    margin: 0;
    position: relative;
    width: 100%;
}
#listcon ul li figure video{
    width:480px; height:320px;
    border: 15px solid #ddd;
    border-radius:30px;
    box-sizing: initial;
    object-fit: cover;
    overflow: hidden;
}
#listcon ul li figure p{    
    position: absolute;
    left:3%; top: 4%;
    background-color: rgba(255,255,255,0.5);
    width:480px; height:320px;
    border-radius: 15px;
    margin: 0;
    z-index: 2;
    opacity: 0;
}
#listcon ul li:hover figure p{
    opacity: 1;
}
#listcon ul li figure p a{
    position: absolute;
    left: 50%; top: 50%;
    transform: translate(-50%,-50%);
    background-color: #F93700;
    color: #fff;
    padding: 5px 30px;
    font-size: 1em;
    border-radius: 10px;
}
#listcon ul li div{ 
    position: relative;
    width: 100%;
    padding: 20px 100px;
    text-align: center;
}
#listcon ul li div h2{
    color: #F93700;
    padding: 10px 0 20px;
}
#listcon ul li div p{
    font-family: sans-serif;
    color: #5E6554;
    font-size: 0.9em;
    line-height: 2;
}
#listmore{
    text-align: center;
    padding: 100px 0;
    margin-left: 15%;
}
#listmore span{
    background-color: #F93700;
    padding:6px 50px;
    border-radius: 20px;
}
#listmore span a{
    color: #fff;
    font-weight: 700;
}
