@import url('./css/Common.css');

#main{
  width: 100%; height: 100%;
  margin-top:80px;
  padding: 50px 0 80px;
  position: relative;
}
#maintxt{
  padding-left: 15%;
}
#maintxt h2{
  color: #fff;
  font-size:8.5em;
  padding: 20px 0;
}
#maintxt2{
  padding-right: 15%;
}
#maintxt2 h2{
  color: #F93700;
  font-size:8em;
  text-align: end;
  padding: 30px 0 0;
  
  border-bottom: 1px solid #fff;
}
#mainsub{
  position: absolute;
  right: 15%; top: 30%;

  text-align: end;
  color: #919191;
}
#mainsub p{
  font-size: 0.9em;
  line-height: 2;
  padding-bottom: 10px;
}
#mainsub span{
  line-height:1.5;
  font-size: 0.8em;
}
#main img{
  position: absolute;
  right: 0; top: 0;
  width: 74%;
  z-index: -1;
}
#main p{
  color: #919191;
  font-size: 0.9em;
  line-height: 1.5;
  padding-left: 15%;
  padding-top: 15px;
}
#mainfoot p{
  color: #919191;
  font-size: 0.8em;
  text-align: center;
}