@import url('./Common.css');

#project3{
    width: 100%; height: 100%;
    margin-top: 80px;
    background-color: #fff;
}

#promain3{
  width: 100%; height:800px;
  margin-top: 80px;
  position: relative;
}
#promain3::before{
  content: '';
  width: 78%;
  border-bottom: 2px solid #F93700;
  position: absolute;
  right: 0; top:19%;
}
#promain3 h1{
  color: #F93700;
  font-size: 5em;
  text-align: end;

  position: absolute;
  left: 7%; top: 23%;
  transform: rotate(270deg) translateY(100%);
  transform-origin: 0 100%;
}
#plist3{
  position: absolute;
  left: 22%; top: 11%;
  padding: 20px 0;
}
#plist3 a{
  background-color: #F93700;
  border-radius: 15px;
  padding: 8px 40px;
  display: inline-block;
  color: #fff;
  font-size: 0.9em
}
#plist3 a b{
  margin-left: 20px;
}
#pmain3{
  width: 70%;
  position: absolute;
  left:23%; bottom: 25%;

  display: flex;
  justify-content: space-between;
  align-items: flex-end;
}
#pmaintxt3 {
  width: 40%;
}
#pmaintxt3 h3{
  font-size: 1.8em;
  text-align: end;
  padding-bottom: 20px;
}
#pmaintxt3 h3 a{
  color: #F93700;
  position:relative;
}
#pmaintxt3 h3 a::after{
  content:'';
  position: absolute;left:0; bottom:-5px;
  border-bottom: 2px solid #F93700;
  width:0;
  transition:.5s;  
}
#pmaintxt3 h3 a:hover::after{
  width:100%;
}


#pmaintxt3 div{
  border-bottom: 1px solid #DBD5C9;
  width: 100%;
  text-align: end;
  padding: 20px 0;
}
#pmaintxt3 p{
  color: #5E6554;
  font-size: 1em;
}
#pmaintxt3 p a{
  display: inline-block;
  margin-left: 20px;
  transform: rotate(90deg);
}
#pmaintxt3 span{
  color: #5E6554;
  font-size: 1em;
  padding-right: 20px;
  line-height: 2;
  display: none;
}
#pmain3 video{
  width:560px; height: 350px;
  border:15px solid #ddd;
  border-radius:60px;
  box-sizing: initial;
  object-fit: cover;
  overflow: hidden;
}

#psub3{background-color: #DBD5C9;
  width: 100%; height: 650px;
  display: flex;
  justify-content: center;
  align-items: center;
  
}
#psub3vid{
  width: 50%; height: 520px;
}
#psub3vid video{
  width: 100%; height: 100%;
  object-fit: fill;
  overflow: hidden;
  border:15px solid #fff;
  border-radius:60px;
}
#psub3txt{
  width: 40%; height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
#psub3txt h3{
  color:#F93700;
  font-size: 2.5em;
  line-height: 3;
}
#psub3txt p{
  font-family: sans-serif;
  color: #fff;
  word-break: keep-all;
  font-size: 0.9em;
  line-height: 2;
  text-align: center;
}

#psub3con2{
  width: 100%;
  padding: 100px 0;
  margin: 100px 0;
}
#psub3con2 p::before{
  content: '';
  display: block;
  width: 10%;
  padding-bottom: 20px;
  margin: 0 auto;
  border-top: 2px solid #DBD5C9;
}
#psub3con2 p{
  color: #DBD5C9;
  line-height: 2;
  text-align:center;
}

#psub3con3{
  width: 100%; height:800px;
  position: relative;
}

#psub3con3left{
  width: 45%;
  position: absolute;
  left: 0; top: 0;
  z-index: 2;
  text-align: start;
}
#psub3con3left img{
  width:600px; height: 350px;
  border:15px solid #ddd;
  border-radius:60px;
  box-sizing: initial;
  object-fit: cover;
  overflow: hidden;
}
#psub3con3txt{
  width: 95%;
  padding:150px 50px 0;
  text-align: end;
}
#psub3con3txt h3{
  color: #fff;
  font-size: 1.2em;
  padding: 10px 0;
}
#psub3con3txt p{
  font-size: 1em;
  color: #fff;
  line-height: 1.8;
  padding-left:50px;
}
#psub3con3right{
  position: absolute;
  right: 0;bottom: 10%;
  width: 75%; height:600px;
  background-color: #F93700;
  text-align: end;
  padding:80px 0;
}
#psub3con3right img{
  width:650px; height: 420px;
  border:15px solid #ddd;
  border-radius:60px;
  box-sizing: initial;
  object-fit: cover;
  overflow: hidden;
}
