    display: flex;
    justify-content: space-between;
  }
  #nav ul{
    display: flex;
    padding: 0;
  }
  #nav ul li{
    padding: 30px 40px;
  }
  
  #nav ul li a{
    color: #F93700;
    font-size:0.9em;
    font-weight: 800;
  }
  #nav ul li a:hover{
    border-bottom: 2px solid #fff;
    color: #fff;
  }
  #navIcon ul{
    display: flex;
    padding: 0;
  }
  #navIcon ul li{
    padding:27px 15px;
  }
  #navIcon ul li a{
    color: #F93700;
  }
