@import url('./Common.css');

#intro{
    width: 100%; height: 100%;
    margin-top: 80px;
    background-color: #fff;
}
#imain{
    width: 100%; height: 100%;
    padding: 100px 0;
    background-color: #DBD5C9;
    position: relative;
}
#imain::before{
    content: '';
    width: 78%;
    border-bottom: 2px solid #F93700;
    position: absolute;
    right: 0; top:16%;
}
#imain h1{
    color: #F93700;
    font-size: 5em;
    text-align: end;

    position: absolute;
    left:7%; top:32%;
    transform: rotate(270deg) translateY(100%); 
    transform-origin: 0 100%
}
#intro1{
    width:70%;
    margin-left:25%;
    margin-top: 8%;
    padding: 50px;
    text-align: end;
}
#intro1 h2{
    color: #F93700;
    font-size:1.6em;
    padding-bottom:40px;
    display: inline-block;
}
#intro1 p b{
    font-family: sans-serif;
    display: block;
    padding: 15px 0;
    font-size:1.2em;
}
#intro1 p{
    font-family: sans-serif;
    font-weight: 500;
    color: #fff;
    font-size: 0.9em;
    line-height: 2;
    word-break: keep-all;
}

#intro2{
    width: 100%;
    padding: 100px 0;
    position: relative;
    text-align: center;
}
#intro2 h2{
    color: #F93700;
    font-size:1.6em;
    padding: 15px 0;
}
#intro2 h2::after{
    content: '';
    display: block;
    width: 15%;
    margin: 0 auto;
    border-bottom: 2px solid #F93700;
}
#introtxt2{
    width: 80%;
    margin: 0 auto;
    padding: 20px 50px;
}
#introtxt2 p{
    color: #919191;
    font-size: 0.9em;
    line-height: 2;
    word-break: keep-all;
    font-family: sans-serif;
}
#introtxt2 p b{
    font-family: sans-serif;
    display: block;
    padding: 15px 0;
    font-size:1.2em;
    color: #919191;
}

#intro3{
    width: 100%;
    background-color: #DBD5C9;
    text-align: center;
}
#intro3 p{
    color: #fff;
    font-size: 4.8em;
    padding:100px 0;
    line-height: 1.5;
    letter-spacing:4px;
}
#intro3 p b{
    color: #F93700;
}

#intro4{
    width: 100%; height:740px;
    padding:60px 0;
}
#intro4::after{
    content: '';
    display: block;
    border-bottom: 2px solid #DBD5C9;
    width:60%;
    margin: 0 auto;
    
}
#intro4 h2{
    color: #F93700;
    font-size:1.6em;
    border-bottom: 2px solid #F93700;
    text-align: end;
    width: 30%;
}
#introtxt4{
    width: 80%; height: 100%;
    margin: 0 auto;
    padding: 50px 50px 0;
}
#introtxtleft{
    float: left;
    width: 48%;
}
#introtxtleft p{
    font-family: sans-serif;
    color: #919191;
    font-size: 0.9em;
    line-height: 2;
    word-break: keep-all;
}
#introtxtleft p b{
    font-family: sans-serif;
    display: block;
    color: #919191;
    padding: 15px 0;
    font-size: 1.2em;
}
#introtxtright{
    float: right;
    width: 48%;
    padding:65px 20px 0;
}
#introtxtright p{
    font-family: sans-serif;
    color: #919191;
    font-size: 0.9em;
    line-height: 2;
    word-break: keep-all;
}