@import url('https://fonts.googleapis.com/css2?family=Source+Serif+Pro:wght@300;400;600;700;900&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Noto+Sans+KR:wght@400;500;700;900&family=Open+Sans&family=Source+Serif+Pro:wght@300;400;600;700;900&display=swap');

*{
    box-sizing: border-box;
    font-family: 'Source Serif Pro', serif, 'Noto Sans KR', sans-serif;
  }
  a{text-decoration: none;}
  ul,li{margin: 0; list-style: none;}
  html,body{margin: 0; padding:0;}
  h1,h2,h3,h4,p,span{margin: 0;cursor: default}
  html{
    width: 100%;
    position: relative;
  }
  body{
  background-color: #DBD5C9;
  }
  header{
  background-color: #DBD5C9;
  width: 100%;
  position: fixed;
  top: 0;
  z-index: 10;
  border-bottom: 1px solid #fff;
  }

  #head{
    width: 90%;
    margin: 0 auto;

    display: flex;
    justify-content: space-between;
  }
  #nav ul{
    display: flex;
    padding: 0;
  }
  #nav ul li{
    padding: 30px 40px;
  }
  
  #nav ul li a{
    color: #F93700;
    font-size:0.9em;
    font-weight: 800;
  }
  #nav ul li a:hover{
    border-bottom: 2px solid #fff;
    color: #fff;
  }
  #navIcon ul{
    display: flex;
    padding: 0;
  }
  #navIcon ul li{
    padding:27px 15px;
  }
  #navIcon ul li a{
    color: #F93700;
  }