@import url('./Common.css');

#about{
    width: 100%; height: 100%;
    margin-top:80px;
    background-color: #fff;
  }
#amain{
  width: 100%; height: 100%;
  padding: 100px 0 50px;
  position: relative;
}
#amain::before{
  content: '';
  width: 78%;
  border-bottom: 2px solid #F93700;
  position: absolute;
  right: 0; top:20%;
}
#amain h1{
  color: #F93700;
  font-size: 5em;
  text-align: end;
  
  position: absolute;
  left:7%; top: 15%;
  transform: rotate(270deg) translateY(100%); 
  transform-origin: 0 100%;
}
#atit{
  width:50%;
  margin-left:35%;
  margin-top: 8%;
  padding-bottom: 100px;
  text-align: end;

  display: flex;
  position: relative;
}

#atxt{
  width: 50%;
  color: #919191;
  padding-top: 230px;
}
#atxt h3{
  font-size: 1.5em;
  line-height: 1.5;
}
#atxt p{
  padding-top:50px;
  line-height: 1.5;
}

#aimg{
  width: 50%;
}
#aimg img{
    width:350px; height: 400px;
    object-fit: cover;
    overflow: hidden;
}
#aimg h2{
  position: absolute;
  right:17%; bottom: 3%;
  color: #F93700;
  font-size:9em;
}

#asub{
  width: 100%; height: 100%;
  padding: 100px 0;
}
#asub::before{
  content: '';
  border-top: 2px solid #DBD5C9;
  display: block;
  width: 60%;
  margin: 0 auto;
  padding-bottom:80px;
}
#asubtit{
  width: 100%;
  padding-bottom: 100px;
  text-align: center;
  font-size: 4em;
  line-height: 1.5;
  color: #F93700;
}
#asubtit p:nth-of-type(1){
  color: #DBD5C9;
}
#asubtit h3 a{
  font-weight: 500;
  color: #F93700;
  position: relative;
}
#asubtit h3 a::after{
  content: '';
  position: absolute; left: 0; bottom:-5px;
  border-bottom: 2px solid #000;
  width: 0;
  transition: .3s;
}
#asubtit h3 a:hover{
  color: #000;
  font-weight: 600;
}
#asubtit h3 a:hover::after{
  color: #000;
  width: 100%;
}
#asubcon{
  width: 100%; height: 550px;
}
#aleft{
  float: left;
  width: 31%; height: 100%;
  background-color: #DBD5C9;
  padding: 40px 120px;
}
#aleft p{
  color: #F93700;
  font-size: 1.2em;
  padding: 30px 0;
  font-weight: 600;
}
#aleft b{
  font-size: 2em;
}
#aright{
  float: right;
  width: 68%; height: 100%;
  background-color: #F93700;
  padding-left: 100px;
  padding-top: 100px;
}
#aright p{
  font-family: sans-serif;
  color: #fff;
  font-size: 1em;
  line-height: 2;
  padding-bottom:60px;
}
#aright p:nth-of-type(4){
  padding: 0;
}

#asubtwo{
  width: 100%;
  padding:20px 0;
  text-align: center;
}
#asubtwo h2{
  width: 100%;
  background-color: #F93700;
  padding: 10px 0;
  color: #fff;
  margin-bottom: 50px;
  letter-spacing: 3px;
  overflow: hidden;
}
#asubtwo span{
  font-size:9em;
  font-weight:600;
  color: #DBD5C9;
}
#asubtwo span:hover{
  color: rgba(0,0,0,0);
  -webkit-text-stroke: 2px #F93700;

}
